<template>
  <div>
  
   <div v-if="editor" class="wr_btn">
    
    <button @click="editor.chain().focus().toggleBold().run()" :disabled="!editor.can().chain().focus().toggleBold().run()" :class="{ 'is-active': editor.isActive('bold') }">
      <img  src="../assets/images/format_bold.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleItalic().run()" :disabled="!editor.can().chain().focus().toggleItalic().run()" :class="{ 'is-active': editor.isActive('italic') }">
      <img  src="../assets/images/format_italic.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleStrike().run()" :disabled="!editor.can().chain().focus().toggleStrike().run()" :class="{ 'is-active': editor.isActive('strike') }">
      <img  src="../assets/images/format_underlined.svg"/>
    </button>
    <input
      type="color"
      @input="editor.chain().focus().setColor($event.target.value).run()"
      :value=color
    >   
    <button @click="editor.chain().focus().setTextAlign('left').run()" :class="{ 'is-active': editor.isActive({ textAlign: 'left' }) }">
      <img  src="../assets/images/format_align_left.svg"/>
    </button>
    <button @click="editor.chain().focus().setTextAlign('center').run()" :class="{ 'is-active': editor.isActive({ textAlign: 'center' }) }">
      <img  src="../assets/images/format_align_center.svg"/>
    </button>
    <button @click="editor.chain().focus().setTextAlign('right').run()" :class="{ 'is-active': editor.isActive({ textAlign: 'right' }) }">
      <img  src="../assets/images/format_align_right.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleHeading({ level: 1 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 1 }) }">
      <img  src="../assets/images/format_h1.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleHeading({ level: 2 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 2 }) }">
      <img  src="../assets/images/format_h2.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleHeading({ level: 3 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 3 }) }">
      <img  src="../assets/images/format_h3.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleHeading({ level: 4 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 4 }) }">
      <img  src="../assets/images/format_h4.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleHeading({ level: 5 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 5 }) }">
      <img  src="../assets/images/format_h5.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleHeading({ level: 6 }).run()" :class="{ 'is-active': editor.isActive('heading', { level: 6 }) }">
      <img  src="../assets/images/format_h6.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleBulletList().run()" :class="{ 'is-active': editor.isActive('bulletList') }">
      <img  src="../assets/images/format_list_bulleted.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleOrderedList().run()" :class="{ 'is-active': editor.isActive('orderedList') }">
      <img  src="../assets/images/format_list_numbered.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleCodeBlock().run()" :class="{ 'is-active': editor.isActive('codeBlock') }">
      <img  src="../assets/images/code_blocks.svg"/>
    </button>
    <button @click="editor.chain().focus().toggleBlockquote().run()" :class="{ 'is-active': editor.isActive('blockquote') }">
      <img  src="../assets/images/format_quote.svg"/>
    </button>
    <button @click="editor.chain().focus().setHorizontalRule().run()">
      <img  src="../assets/images/horizontal_rule.svg"/>
    </button>
    <button @click="editor.chain().focus().undo().run()" :disabled="!editor.can().chain().focus().undo().run()">
      <img  src="../assets/images/undo.svg"/>
    </button>
    <button @click="editor.chain().focus().redo().run()" :disabled="!editor.can().chain().focus().redo().run()">
      <img  src="../assets/images/redo.svg"/>
    </button>
    <input type="file" id="image" class="imageup" @change="addImage">
    <label for="image"><img  src="../assets/images/imagesmode.svg"/></label>
    <button id="add" @click="addVideo">
      <img  src="../assets/images/youtube_activity.svg"/>
      </button>
  </div>
  <editor-content :editor="editor" />
</div>
</template>

<script>
import { Editor, EditorContent } from '@tiptap/vue-3'
import { Color } from '@tiptap/extension-color'
import TextStyle from '@tiptap/extension-text-style'
import StarterKit from '@tiptap/starter-kit'
import Youtube from '@tiptap/extension-youtube'
import Document from '@tiptap/extension-document'
import Heading from '@tiptap/extension-heading'
import Paragraph from '@tiptap/extension-paragraph'
import Text from '@tiptap/extension-text'
import TextAlign from '@tiptap/extension-text-align'
import Image from '@tiptap/extension-image'
import Dropcursor from '@tiptap/extension-dropcursor'

import axios from 'axios'
const url = 'http://localhost:8500/image'

export default {
  components: {
    EditorContent,
  },
  props:{
    modelValue: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      editor: null,
      imgurl:null,
      imgID:null,
      color:'#000000'
    }
  },

  mounted() {
    this.editor = new Editor({
      extensions: [
        StarterKit,
        Document,
        Paragraph,
        Text,
        TextStyle,
        Color,
        Image,
        Dropcursor,
        Heading,
        TextAlign.configure({
          types: ['heading', 'paragraph'],
        }),
        Youtube.configure({
          controls: false,
        }),
      ],
      content: this.modelValue,
      onUpdate: () => {
        this.$emit('update:modelValue', this.editor.getHTML());
      },
      editorProps: {
        attributes: {
          spellcheck: 'false',
        },
      },
    })
    
  },
  watch:{
    modelValue(val){
      if(this.editor.getHTML() === val) return;
      this.editor.commands.setContent(this.modelValue, false)
    }
  },

  methods: {
    clearContent() {
        this.editor.commands.clearContent();
    },
    async addImage(event) {
      const file = event.target.files[0]; 
      if (!file) return;

      const formData = new FormData();
      formData.append('image', file); 

      await axios.post(url, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      })
      .then(res => {
        const id = res.data.data.id;
        const url = res.data.data.url;
        this.editor.chain().focus().setImage({ src: url }).run();
        console.log(id)
        console.log(url)
        this.$emit('imgurlHandle', url);
        this.$emit('imgIDHandle', id);
      })
      .catch(error => {
        console.error('Image upload failed:', error);
      });
    },

    addVideo() {
      const url = prompt('YouTube 주소를 입력하세요')

      if(url){
        this.editor.commands.setYoutubeVideo({
          src: url,
        })
      }

      
    }
  },

  beforeUnmount() {
    this.editor.destroy()
  },
}
</script>



<style lang="scss">
.wr_btn{margin: 15px auto;}
.wr_btn button, .wr_btn label{background-color: transparent; border: none;cursor: pointer;margin: 4px 1px;}
.wr_btn .imageup{display: none;}
/* Basic editor styles */
.tiptap{padding: 20px;max-height: calc(100vh - 550px);height: 70vh; background: #FFF;overflow: scroll;box-sizing: border-box;border: 1px solid #000;}


.tiptap {
  > * + * {
    margin-top: 0.75em;
  }

  img{max-width: 95%;}

  strong{font-weight:bold;}
  em{font-style: italic;}

  ul,
  ol {
    padding: 0 1rem;
  }

  ul{list-style: disc;}
  ol{list-style: decimal;}

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: 1.1;
    font-weight: bold;
  }

  h1{font-size: 2.5em;}
  h2{font-size: 2.2em;}
  h3{font-size: 1.9em;}
  h4{font-size: 1.6em;}
  h5{font-size: 1.3em;}
  h6{font-size: 1em;}

  code {
    background-color: rgba(#616161, 0.1);
    color: #616161;
  }

  pre {
    background: #0D0D0D;
    color: #FFF;
    font-family: 'JetBrainsMono', monospace;
    padding: 0.75rem 1rem;
    border-radius: 0.5rem;

    code {
      color: inherit;
      padding: 0;
      background: none;
      font-size: 0.8rem;
    }
  }

  img {
    max-width: 100%;
    height: auto;
  }

  blockquote {
    padding-left: 1rem;
    border-left: 2px solid rgba(#0D0D0D, 0.1);
    margin: 1em 40px;
  }

  hr {
    border: none;
    border-top: 2px solid rgba(#0D0D0D, 0.1);
    margin: 2rem 0;
  }
}
</style>