<template>
    <section class="sec1 sec" data-aos="fade-up">
        
    </section>
    <section class="sec sec2" data-aos="fade-up">
        <div class="inner">
            <dl>
                <dt>
                    <h4>blog</h4>
                    <router-link to="/blog">전체보기 +</router-link>
                </dt>
                <swiper
                    :slidesPerView="1"
                    :spaceBetween="10"
                    :space-between="50"
                    :pagination="{
                        clickable: true,
                    }"
                    :breakpoints="{
                    '640': {
                        slidesPerView: 1,
                        spaceBetween: 20,
                    },
                    '768': {
                        slidesPerView: 2,
                        spaceBetween: 40,
                    },
                    '1024': {
                        slidesPerView: 3,
                        spaceBetween: 50,
                    },
                    }"
                    :modules="modules"
                    @swiper="onSwiper"
                    @slideChange="onSlideChange"
                >
                    <swiper-slide v-for="(item, index) in post" :key="index" class="dd">
                        <router-link :to="'/detail/'+item.id">
                            <div class="img_wrap">
                                <img :src='item.imgurl'/>
                            </div>
                            <h4>{{item.title}}</h4>
                        </router-link>
                    </swiper-slide>
                </swiper>
                
            </dl>
        </div>
    </section>
</template>

<script>
const url = 'http://118.67.135.87:8000/blog'
import axios from 'axios';
import { Swiper, SwiperSlide } from 'swiper/vue';
import { Pagination } from 'swiper/modules';
import 'swiper/css/pagination';
import 'swiper/css';
import AOS from 'aos';
import 'aos/dist/aos.css';

AOS.init();

export default {
    name: "HomeView",
    data(){
        return{
            post: null
        }
    },
    components: {
      Swiper,
      SwiperSlide,
    },
    methods: {
        async axiosHandle(){
            try{
                await axios.get(url).then((res)=>{
                    if(res.data.success){
                        this.post=res.data.data
                    }else{
                        console.error('error')
                    }
                })
            }catch(error){
                console.error('Error fetching data:', error);
            }
        }
    },
    mounted() {
        this.axiosHandle();
    },
    setup() {
      const onSwiper = (swiper) => {
        console.log(swiper);
      };
      const onSlideChange = () => {
        console.log('slide change');
      };
      return {
        onSwiper,
        onSlideChange,
        modules: [Pagination],
      };
    },
}
</script>

<style scoped>
    .sec1{background: url(../assets/images/sec1.jpg) no-repeat center;background-size: cover; height: 50vh;}
    .sec2{padding: 50px 0;background: #ddd;}
    .sec2 dl{display: flex;}
    .sec2 dl dt{width: 25%;display: flex;flex-direction: column;justify-content: center;text-align: center;}
    .sec2 dl .dd{width: 25%;padding: 20px 0;margin: 1%;cursor: pointer;}
    .sec2 dl .dd img{width: 100%;height: 300px;object-fit: cover;}

    @media screen and (max-width:1200px) {
        .sec1{height: 30vh;}
        .sec2 dl{flex-wrap: wrap;}
        .sec2 dl dt{width: 100%;}
    }
</style>