<template>
    <SubtopCom :title="title"></SubtopCom>
    <div class="wrapper">
        <div v-if="$store.state.user" class="intro">
            <h3>{{$store.state.user}}님, 반갑습니다.</h3>
        </div>
        <form @submit.prevent="handleSubmit" v-else>
            <input 
                ref="usernameInput"
                type="text"
                name="username"
                placeholder="이름"
                v-model="initial_state.username"
            />
            <input 
                ref="useremailInput"
                type="email"
                name="useremail"
                placeholder="email"
                v-model="initial_state.useremail"
            />
            <input 
                ref="userpwdInput"
                type="password"
                name="userpwd"
                placeholder="password"
                v-model="initial_state.userpwd"
            />
            <div class="btn_wrap">
                <button @click="handlelogin">회원가입</button>
                <button @click="handleReset">취소</button>
            </div>
            
        </form>
    </div>
</template>

<script>
import SubtopCom from '@/components/SubtopCom.vue';
import axios from 'axios';

export default {
    name: 'RegisterView',
    components:{
        SubtopCom
    },
    data(){
        return{
            title:'회원가입',
            initial_state : {
                username: "",
                useremail: "",
                userpwd: "",
            },
        }
        
    },
    methods:{
        async handlelogin(){
            const url = 'http://118.67.135.87:8000/register'
            await axios.post(url, this.initial_state)
            .then(res=>{
                if (res.data.success) {
                    localStorage.setItem('accessToken', res.data.accessToken);
                    this.handleReset();
                    let user = res.data.message
                    this.$store.dispatch('login', user);

                } else {
                    console.error(res.data.message);
                }
            }).catch(error => {
                console.error('Error during login:', error);
            });
        },
        handleSubmit(e){
            e.preventDefault();
        },
        handleReset(){
            this.$refs.usernameInput.value = '';
            this.$refs.useremailInput.value = '';
            this.$refs.userpwdInput.value = '';
            this.$refs.usernameInput.focus();
        }
    }

}
</script>

<style scoped>
    form input{width: 90%;max-width: 450px;display: block;border-top: 1px solid #000;border-bottom: 1px solid #000;border-left: none;border-right: none;margin: 15px auto;line-height: 40px;padding: 8px;}
    .btn_wrap{width: 90%;max-width: 450px;margin: 30px auto;display: flex;flex-direction: row;justify-content: center;}
    .btn_wrap a, .btn_wrap button{width: 30%;text-align: center;border: 1px solid #000;line-height: 40px;background: #fff;transition: all 0.3s ease;cursor: pointer;margin: 0 10px;}
    .btn_wrap a:hover, .btn_wrap button:hover{background: #000;color: #FFF;}
    .intro{height: 300px;display: flex;flex-direction: column;justify-content: center;text-align: center;}
    .intro h3{font-size: 24px;}
</style>