<template>
    <div class="write_wrap" :class="write === true ? 'on' : null">
        <div class="container">
            <div class="inner">
                <div class="top"><img  class="close" src="../../assets/images/close.svg" @click="$emit('writeHandle')" /></div>
                <h2 class="wr_tit">글쓰기</h2>
                <dl>
                    <dd><input type="text" placeholder="제목" v-model="result.title"/></dd>
                 </dl>   
                
                 <TiptapCom ref="editor" v-model="result.content" @imgurlHandle="updateImgUrl" @imgIDHandle="updateImgID" @clearContent="clearContent" />
                 <div class="btn_wrap">
                    <button @click="onSubmit" class="write_btn">글쓰기</button>
                    <button @click="reset" class="write_btn">다시쓰기</button>
                 </div>
                
            </div>
        </div>
    </div>

</template>

<script>

const url = 'http://localhost:8500/blog'

import axios from 'axios';
import TiptapCom from '../TiptapCom.vue'
export default {
    name: "BlogWrite",
    components: {
        TiptapCom,
    },
    props:{
        write: Boolean 
    },
    data(){
      return{
        result:{
            title: null,
            content: '',
            writer: this.$store.state.user,
            imgurl:'https://kr.object.ncloudstorage.com/gyulstorage/gyultree/IT_tid044t000819%20%281%29.jpg',
            imgID:null
        }
      }  
    },
    methods:{
        clearFields() {
            this.result.title = '';
            this.result.content = '';
            this.result.writer = '';
            this.result.imgurl = 'https://kr.object.ncloudstorage.com/gyulstorage/gyultree/IT_tid044t000819%20%281%29.jpg';
            this.result.imgID = null;
        },
        reset(){
            this.$refs.editor.clearContent();
            this.clearFields();
        },
        async onSubmit(){
            try{
                await axios.post(url, this.result).
                then(res => {
                    if(res.data.success){
                        console.log(res.data.data)
                        this.$refs.editor.clearContent();
                        this.clearFields();
                        this.$emit('postSuccess', res.data.data);
                    }else{
                        console.error('error')
                    }
                })
            }catch(error){
                console.error('Error fetching data:', error);
            }
        },
        updateImgUrl(url) {
            this.result.imgurl = url;
        },
        updateImgID(id) {
            this.result.imgID = id;
        },
    },
    watch:{
        result(){

        }
    }

}
</script>

<style scoped>
    .inner{height: calc(100% - 30px);}
    
    .wr_tit{text-align: center;font-size: 24px;font-weight: bold;}
    .write_wrap{position: fixed;background: #000000c2;width: 100vw;height: 100vh;top: 0;left: 0;display: none;flex-direction: column;justify-content: center;}
    .write_wrap.on{display: flex;}
    .container{height: 80vh;max-width: 1100px;margin: 0 auto;width: 100%;background: #fff;}
    .top{display: flex;flex-direction: row;justify-content: flex-end;margin: 15px 0;}
    .top .close{cursor: pointer;}
    dl{display:flex; justify-content: flex-start;margin: 10px 0;flex-wrap: wrap;flex-direction: row;}
    dl dt{width: 300px;margin: 10px 15px 0 0;}
    dl dd{width: 600px;margin: 10px 0;}
    dl dt input, dl dd input{width: 95%;line-height: 30px;}
    dl dd input{width: 100%;line-height: 30px;}
    .btn_wrap{display: flex;flex-direction: row;justify-content: center;}
    .write_btn{margin: 15px 5px;display: block;width: 100px;line-height: 35px;border: 1px solid #000;background: #fff;cursor: pointer;}
    .write_btn:hover{background: #000;color: #fff;}

    @media screen and (max-width:1100px) {
        dl dd{width: 100%;}
        .write_wrap{top:35px}
        .container{overflow-y: scroll;height: calc(100vh - 60px);}
    }
</style>