<template>
    <div>
    <SubtopCom :title="title"></SubtopCom>
    <div class="wrapper" data-aos="fade-up">
        <ul class="listul">
            <li v-for="(item, index) in post" :key="index" >
                <router-link :to="'/detail/'+item.id">
                    <div class="img_wrap">
                        <img :src='item.imgurl'/>
                    </div>
                    <h4>{{item.title}}</h4>
                </router-link>
            </li>
        </ul>
        <div class="ctrl">
            <button class="btn" @click="writeHandle">글쓰기</button> 
        </div>
        
    </div>
    <BlogWrite :write="write" @postSuccess="reloadhandle" @writeHandle="write = false"/>   
</div>
</template>

<script>
import SubtopCom from '../components/SubtopCom.vue'
import BlogWrite from '../components/Blog/BlogWrite.vue'

import axios from 'axios'

const url = 'http://118.67.135.87:8000/blog'

export default {
    name: "BlogView",
    data(){
        return{
            title : "blog",
            post: null,
            write: false
        }
    },
    components: {
        SubtopCom,
        BlogWrite
    },
    methods: {
        async axiosHandle(){
            try{
                await axios.get(url).then((res)=>{
                    if(res.data.success){
                        this.post=res.data.data
                    }else{
                        console.error('error')
                    }
                })
            }catch(error){
                console.error('Error fetching data:', error);
            }
        },
        writeHandle(){
            this.write = !this.write;
        },
        reloadhandle() {
            window.location.reload();
        }

    },
    mounted() {
        this.axiosHandle();
    },
    
}
</script>

<style scoped>
    .write_btn{padding: 20px;background: #c8cfa1;border-radius: 20px;}
    .write_btn button{background: #fff;border: none;width: 100%;line-height: 40px;text-align: left;border-radius: 10px;cursor: pointer;}
    .listul{display: flex;flex-direction: row;justify-content: flex-start;flex-wrap: wrap;}
    .listul li{width: calc(25% - 4%);margin: 2%;}
    .listul li .img_wrap img{width: 100%;height: 250px;object-fit: cover;box-shadow: 2px 3px 3px rgba(0,0,0,0.2);}
    .listul li h4{font-weight: bold;margin: 8px 0;}

    .ctrl{display: flex;flex-direction: row;justify-content: space-between;}
    .ctrl .btn{background: #efefef;border:none;padding: 0 10px;height: 40px;line-height: 40px;cursor: pointer;transition: all ease 0.3s;}
    .ctrl .btn:hover{background: #000;color: #fff;}

    @media screen and (max-width:1100px) {
        .listul li{width: calc(50% - 3%); margin: 1.5%;}
    }
</style>