import {createRouter, createWebHistory } from "vue-router";
import BlogView from "@/routes/BlogView.vue";
import HomeView from "@/routes/HomeView.vue";
import DetailView from "@/routes/DetailView.vue";
import NotfoundView from "@/routes/NotfoundView.vue";
import LoginView from "@/routes/LoginView.vue";
import RegisterView from "@/routes/RegisterView.vue";

const router = createRouter({
    history: createWebHistory(),
    routes: [        
        { path: '/', component: HomeView },
        { path: '/login', component: LoginView },
        { path: '/register', component: RegisterView },
        { path: '/blog', component: BlogView },
        { path: '/detail/:id', component: DetailView,},
        { path: '/:pathMatch(.*)*', component: NotfoundView,},
    ],
})

export default router;