<template>
  <SubtopCom :title="title"></SubtopCom>
  <div class="wrapper" v-if="post" data-aos="fade-up">
    <div class="top">
      <h2>{{post.title}}</h2>
      <div class="info">
        <ul>
          <li>작성자: {{post.name}}</li>
          <li>{{formtime}}</li>
        </ul>
        <div class="ctrl">
          <router-link to="/blog">목록</router-link>
          <button @click="deleteHandle" v-if="$store.state.user===post.name ">삭제</button>
        </div>
      </div>
    </div>
    <div v-html="post.content" class="content"></div>
    <editCom/>
  </div>
  <loading v-else/>
</template>

<script>
import axios from 'axios';
import SubtopCom from '../components/SubtopCom.vue'



export default {
  name:'DetailView',
  components: {
        SubtopCom
  },
  data(){
    return{
      title: 'blog',
      post: null,
      formtime: null,
    }
  },
  created(){
    const id = this.$route.params.id;
    this.axiosHandle(id);
  },
  
  methods: {
    async axiosHandle(id){
      const url = `http://118.67.135.87:8000/detail/${id}`
        try{
            await axios.get(url)
            .then((res)=>{
                if(res.data.success){
                    this.post=res.data.data[0]
                    const postTime = new Date(this.post.time);
                    const year = postTime.getFullYear();
                    const month = String(postTime.getMonth() + 1).padStart(2, '0');
                    const day = String(postTime.getDate()).padStart(2, '0');
                    const hour = String(postTime.getHours()).padStart(2, '0');
                    const minute = String(postTime.getMinutes()).padStart(2, '0');
                    this.formtime = `${year}-${month}-${day} ${hour}:${minute}`;
                }else{
                    console.error('error')
                }
            })
        }catch(error){
            console.error('Error fetching data:', error);
        }
    },
    async deleteHandle(id){
      const deleteUrl = `http://localhost:8500/detail/${id}`;
        try{
          const deleteRes = await axios.delete(deleteUrl);
          if (deleteRes.data.success) {
            alert('게시글이 성공적으로 삭제되었습니다.');
            this.$router.push('/blog');
          } else {
            console.error('error');
          }
      }catch(error){
          console.error('Error fetching data:', error);
      }
    },
  }
}
</script>

<style scoped>
  .top h2{font-size: 36px;font-weight: bold;padding-bottom: 10px;border-bottom: 1px solid #000;}
  .top .info{display: flex;flex-direction: row;justify-content: space-between;padding: 10px 0;margin-bottom: 50px;border-bottom: 1px solid #000;}
  .top .info ul{display: flex;flex-direction: row;justify-content: flex-start;}
  .top .info ul li{margin-right: 10px;}
  .top .info .ctrl button, .top .info .ctrl a{background-color: transparent;border: none;padding: 6px 10px;cursor: pointer;transition: all 0.3s ease;}
  .top .info .ctrl button:hover, .top .info .ctrl a:hover{background: #000;color: #fff;}
  .content{padding: 12px;}
</style>

<style lang="scss">
.content {
  > * + * {
    margin-top: 0.75em;
  }

  img{max-width: 95%;}

  strong{font-weight:bold;}
  em{font-style: italic;}

  ul,
  ol {
    padding: 0 1rem;
  }

  ul{list-style: disc;}
  ol{list-style: decimal;}

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: 1.1;
    font-weight: bold;
  }

  h1{font-size: 2.5em;}
  h2{font-size: 2.2em;}
  h3{font-size: 1.9em;}
  h4{font-size: 1.6em;}
  h5{font-size: 1.3em;}
  h6{font-size: 1em;}

  code {
    background-color: rgba(#616161, 0.1);
    color: #616161;
  }

  pre {
    background: #0D0D0D;
    color: #FFF;
    font-family: 'JetBrainsMono', monospace;
    padding: 0.75rem 1rem;
    border-radius: 0.5rem;

    code {
      color: inherit;
      padding: 0;
      background: none;
      font-size: 0.8rem;
    }
  }

  img {
    max-width: 100%;
    height: auto;
  }

  blockquote {
    padding-left: 1rem;
    border-left: 2px solid rgba(#0D0D0D, 0.1);
    margin: 1em 40px;
  }

  hr {
    border: none;
    border-top: 2px solid rgba(#0D0D0D, 0.1);
    margin: 2rem 0;
  }
}
</style>